
.bx--tab-content {
  padding-top: 54px;
  padding-left: 2px;
}

a.bx--header__name {
  font-size: 1.5rem;
}

header.bx--header {
  height: 6rem;
}

.bx--data-table th {
  text-align: left;
  vertical-align: middle;
  height: 64px;
}

.bx--data-table td {
    padding-top: 10px;
    text-align: left;
    vertical-align: middle;
    height: 72px;
    padding-bottom: 16px;
}

h2 {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 24px;
}

div.bx--toast-notification.bx--toast-notification--error {
  position: fixed;
  right: 0;
  top: 4rem;
  animation: toast-entry 0.5s ease-in;
  transition: opacity 0.5s linear;
  z-index: 90001;
}

.bx--toast-notification--success {
  position: fixed;
  right: 0;
  top: 4rem;
  animation: toast-entry 0.5s ease-in;
  transition: opacity 0.5s linear;
  z-index: 90001;
}

@keyframes toast-entry {
  0% {
    transform: rotateY(90deg);
    opacity: 0.5;
  }
  50% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}

.bx--snippet--inline .bx--copy-btn__feedback {
    box-shadow: 0 2px 6px rgba(0,0,0,.2);
    z-index: 0;
    width: -webkit-max-content;
    width: max-content;
    min-width: 1.5rem;
    max-width: 13rem;
    height: auto;
    padding: .1875rem 1rem;
    background-color: #393939;
    border-radius: .125rem;
    color: #fff;
    text-align: left;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    font-size: .875rem;
    font-weight: 400;
    line-height: 1.29;
    letter-spacing: .16px;
    display: none;
    overflow: visible;
    box-sizing: content-box;
    margin: auto;
    clip: auto;
}